
.footer__wrapper {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    gap: 15px;
    padding-left: 15px;
}

.footer__list {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}

.footer__title {
    font-size: 34px;
}

.footer__logo {
    font-size: 34px;
}

.footer__left {
    width: 50%;
}

.footer__element {
    display: flex;
    align-items: center;
    gap: 30px;
    margin-top: 40px;
}

.footer__img {
    display: flex;
    align-items: center;
    min-width: 70px;
    max-width: 70px;

}

.footer__img img {
    width: 100%;
}

.footer__text {
    font-size: 28px;
    font-weight: 500;
}

.footer__buttons {
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.footer__right {
    position: relative;
    height: 100vh;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer__right__wrapper {
    display: flex;
    flex-direction: column;
    backdrop-filter: blur(5px); /* Ð­ÑÑÐµÐºÑ ÑÐ°Ð·Ð¼ÑÑÐ¸Ñ ÑÐ¾Ð½Ð° */
    background: rgba(0, 0, 0, 0.14); /* ÐÐ¾Ð»ÑÐ¿ÑÐ¾Ð·ÑÐ°ÑÐ½ÑÐ¹ ÑÐ¾Ð½ */
    border-radius: 30px;
    overflow: hidden;
    padding: 50px;
}

.footer__right__img {
    position: absolute;
    left: 0;
    top: 0;
    height: 100vh;
    width: 100%; /* ÐÐ·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ Ð´Ð¾Ð»Ð¶Ð½Ð¾ Ð·Ð°Ð½Ð¸Ð¼Ð°ÑÑ Ð²ÑÑ ÑÐ¸ÑÐ¸Ð½Ñ ÐºÐ¾Ð½ÑÐµÐ¹Ð½ÐµÑÐ° */
}

.footer__right__img img {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ð¡Ð¾ÑÑÐ°Ð½ÑÐµÐ¼ Ð¿ÑÐ¾Ð¿Ð¾ÑÑÐ¸Ð¸ Ð¸Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ñ */
}

@media (max-width: 770px) {

    .footer__title {
        font-size: 32px;
    }

    .footer__text {
        font-size: 24px;
    }

    .footer__img {
        min-width: 75px;
    }

    .rating__points {
        font-size: 55px;
    }


    .button {
        width: 200px;
    }

}
